<template>
  <div>
    <div class="report_Head">
      <div class="left-header">
        <img class="fl" src="data:image/png;base64," alt="">
        <div class="title">
          <h3>报告查找</h3>
          <p>及时查看报告</p>
        </div>
      </div>
      <div class="right-header">
        <img src="data:image/png;base64," alt="">
        <div class="title">
          <h3>报告推送</h3>
          <p>历史报告找回</p>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: "ReportHead"
}
</script>

<style lang="less" >
@import "../../assets/css/BaseFont";
  .report_Head {
    display: flex;
    justify-content: space-around;
    margin: 20rem / @baseFont auto;
    width: 90%;
    height: 150rem / @baseFont;
    box-shadow: 1px 1px 4px #ccc;
    .left-header{
      border-right: 1px solid #cccccc;
    }
    .left-header , .right-header{
      margin-top: 30rem / @baseFont;
      padding: 0 25rem / @baseFont;
      flex: 1;
      height: 90rem / @baseFont;
      box-sizing: border-box;
      img {
        width: 66rem / @baseFont;
        height: 76rem / @baseFont;
        margin-top: 10rem / @baseFont;
        margin-left: 15rem / @baseFont;
        box-sizing: border-box;
      }
      .title{
        float: right;
        padding-top: 10rem / @baseFont;
        padding-right: 19rem / @baseFont;
        line-height: 40rem / @baseFont;
        box-sizing: border-box;
        h3{
          font-size: 20rem / @baseFont;
        }
        p{
          font-size: 12rem / @baseFont;
          color: #888;
        }
      }
    }
  }
</style>



